import React from 'react'
import { Form } from 'antd'
import { Input, TextArea } from '@/components/input'
import Checkbox from '@/components/checkbox'
import { Modal, ModalProps } from '@/components/report-template-modal'
import TemplateWrapper from '@/components/report-template-wrapper'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/hooks/useReportTemplate'

interface Setting extends ReportTemplateSetting {
  defaultValue?: string
  multipleLine?: boolean
}

export default function ReportTemplateInputText(props: ReportTemplateProps) {
  const { editModalVisible, handleEditModalVisible, onUpdate, item } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({ item, onUpdate })

  const inputProps = {
    bordered: false,
    className: 'w-2/3',
    placeholder: setting.tooltip || '輸入內容',
    defaultValue: setting.defaultValue,
  }

  return (
    <>
      {editModalVisible && (
        <InputTextModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <Form.Item noStyle name={['data', item.id]}>
          {setting.multipleLine ? (
            <TextArea {...inputProps} autoSize={{ minRows: 3 }} />
          ) : (
            <Input {...inputProps} />
          )}
        </Form.Item>
      </TemplateWrapper>
    </>
  )
}

function InputTextModal({
  onOk,
  onCancel,
  item,
  ...props
}: ModalProps<Setting>) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  return (
    <Modal<Setting>
      {...props}
      title={
        <>
          <div>
            Text
            <div className="inline ml-2 text-sm text-gray-600">
              typically used for description something.
            </div>
          </div>
        </>
      }
      onCancel={onCancel}
      onOk={handleOk}>
      <Form
        {...createFormItemLayout(5, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Form.Item label="Text" name="text">
          <Input />
        </Form.Item>
        <Form.Item label="Default Value" name="defaultValue">
          <Input />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
        <Form.Item
          label="Multiple Lines"
          name="multipleLine"
          valuePropName="checked">
          <Checkbox />
        </Form.Item>
      </Form>
    </Modal>
  )
}
