import React from 'react'
import { Form } from 'antd'
import cx from 'classnames'
import { pathOr } from 'ramda'
import { Checkbox, CheckboxGroup } from '@/components/checkbox'
import Input from '@/components/input'
import TemplateWrapper from '@/components/report-template-wrapper'
import {
  TemplateSelectionModal,
  Setting,
  Option,
  defaultValue,
} from '@/components/report-template-selection-modal'
import { mapIndexed } from '@/utils/webHelper'
import { ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/hooks/useReportTemplate'
const { compose } = require('ramda')

function ReportTemplateMultipleSelection(props: ReportTemplateProps) {
  const { item, editModalVisible, handleEditModalVisible, onUpdate } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({
    item,
    onUpdate,
    defaultValue,
  })

  return (
    <>
      {editModalVisible && (
        <TemplateSelectionModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
          title={
            <>
              <div>Multiple Selection List</div>
              <div className="mt-1 text-sm text-gray-600">
                typically used for shorter lists so that all responses are
                visible as checkboxes. Allows Multiple selections.
              </div>
            </>
          }
        />
      )}
      <TemplateWrapper
        title={setting.text || ''}
        bodyStyle={{ paddingLeft: 36 }}>
        <Form.Item noStyle name={['data', item.id]}>
          <CheckboxGroup>
            {compose(
              mapIndexed((x: Option, idx: number) => (
                <Checkbox
                  key={x.id}
                  className={cx('block ml-0', { 'mt-1': idx > 0 })}
                  defaultChecked={false}
                  value={`${idx}`}>
                  {x.text}
                  {x.freetext && (
                    <Input
                      bordered={false}
                      size="small"
                      placeholder="輸入備註"
                      className="ml-4 w-1/3"
                    />
                  )}
                </Checkbox>
              )),
              pathOr([], ['options'])
            )(setting)}
          </CheckboxGroup>
        </Form.Item>
      </TemplateWrapper>
    </>
  )
}

export default ReportTemplateMultipleSelection
