import React from 'react'
import { pathOr, map } from 'ramda'
import { Form } from 'antd'
import Select from '@/components/select'
import TemplateWrapper from '@/components/report-template-wrapper'
import {
  TemplateSelectionModal,
  Option,
  defaultValue,
} from '@/components/report-template-selection-modal'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/hooks/useReportTemplate'
const { compose } = require('ramda')

interface Setting extends ReportTemplateSetting {
  options?: Option[]
  selectionType?: 'single' | 'multiple'
}

export default function ReportTemplateSelect(props: ReportTemplateProps) {
  const { item, editModalVisible, handleEditModalVisible, onUpdate } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({
    item,
    onUpdate,
    defaultValue,
  })

  return (
    <>
      {editModalVisible && (
        <TemplateSelectionModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
          title={
            <>
              <div>Drop-down List</div>
              <div className="mt-1 text-sm text-gray-600">
                typically used for lists of greater than 6 or 7 options.
              </div>
            </>
          }
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <Form.Item noStyle name={['data', item.id]}>
          <Select
            allowClear
            bordered={false}
            className="w-1/3"
            placeholder={setting.tooltip || '請選擇...'}>
            {compose(
              map((x: Option) => (
                <Select.Option key={x.id} value={x.text as any}>
                  {x.text}
                </Select.Option>
              )),
              pathOr([], ['options'])
            )(setting)}
          </Select>
        </Form.Item>
      </TemplateWrapper>
    </>
  )
}
